.popup-container {
  width: 430px;
  background-color: #fff;
  border-radius: 10px;
  position: relative;
}

@media screen and (max-width: 1023px)  {
  .popup-container {
    width: 430px;
  }
}

@media screen and (max-width: 767px)  {
  .popup-container {
    width: 240px;
  }
}
